import { createStore } from 'Redux';
import appReducer from './reducers/gameStatus';
import {
    GAME_FIELD_WIDTH,
    GAME_FIELD_HEIGHT,
    ENEMY_IMAGES_PATHS,
    PLAYER_IMAGES_PATHS
} from './constants';
import {
    changeGameStatus
} from './actions/gameStatus';
import Game from './models/Game';

const body = document.querySelector('body');
const gameField = document.querySelector('canvas');
const gameStatusButton = document.querySelector('#gameStatus');
const score = document.querySelector('#score');
let gameStatus;
let lives;
let game;

gameField.width = GAME_FIELD_WIDTH;
gameField.height = GAME_FIELD_HEIGHT;

const enemyImages = [];
const playerImages = [];

function loadImages(array, paths) {
    for (let i = 0; i < paths.length; i++) {
        const img = new Image();
        img.src = paths[i];
        img.onload = () => {
            array.push(img);
        }
    }
}

document.addEventListener('DOMContentLoaded', () => {
    loadImages(enemyImages, ENEMY_IMAGES_PATHS);
    loadImages(playerImages, PLAYER_IMAGES_PATHS);
});

gameStatusButton.addEventListener('click', (e) => {
    let newGameStatus;
    if (gameStatus === 'GAME_PAUSED' || !gameStatus) {
        newGameStatus = 'START_GAME';
    } else {
        newGameStatus = 'GAME_PAUSED';
    }
    store.dispatch(changeGameStatus(newGameStatus));
    gameStatusButton.classList.toggle('-hidden');
});

function renderGameStatus() {
    if (gameStatus === 'GAME_FAILED') {
        gameStatusButton.textContent = 'PLAY AGAIN';
        gameStatusButton.classList.toggle('-hidden');
        store.dispatch(changeGameStatus('GAME_PAUSED'));
    }
}

const store = createStore(appReducer);
game = new Game(store, gameField, playerImages, enemyImages);
store.subscribe(() => {
    gameStatus = store.getState().gameStatusReducer.gameStatus;
    lives = store.getState().gameStatusReducer.lives;
    console.log(`Game status: ${gameStatus}, lives ${lives}`);
});
store.subscribe(renderGameStatus);
store.subscribe(() => {
    if (gameStatus === 'START_GAME') {
        requestAnimationFrame(game.main.bind(game));
    }
});





