export const changeGameStatus = (status) => {
    return {
        type: 'CHANGE_GAME_STATUS',
        payload: {
            gameStatus: status
        }
    };
};

export const decrementLives = () => {
    return {
        type: 'DECREMENT_LIVES'
    }
};

export const incrementLives = () => {
    return {
        type: 'INCREMENT_LIVES'
    }
};

export const resetLives = () => {
    return {
        type: 'RESET_LIVES'
    }
};


