import { combineReducers } from 'Redux';

const initialState = {
    gameStatus: 'GAME_PAUSED',
    lives : 3
};

const gameStatusReducer = (state = initialState , action) => {
    switch (action.type) {
        case 'CHANGE_GAME_STATUS':
            return {
                ...state,
                gameStatus: action.payload.gameStatus  || state.gameStatus
            };
        case 'RESET_LIVES': {
            return {
                ...state,
                lives: 3
            }
        }
        case 'DECREMENT_LIVES':
            return {
                ...state,
                lives: --state.lives
            };
        case 'INCREMENT_LIVES' :
            return {
                ...state,
                lives: state.lives++
            };
        default:
            return state;
    }
};

export default combineReducers({
    gameStatusReducer
});





