const PLAYER_SPEED = 200;


export default class Player {
    constructor(x, y, sprite) {
        this.x = x;
        this.y = y;
        this.sprite = sprite;
    }

    update(context, input, dt, height, width) {
        this.updatePosition(input, dt);
        this.checkBounds(height, width);
        this.sprite.update();
        this.sprite.render(context, this.x, this.y);
    }

    updatePosition(input, dt) {
        if (input.isDown('DOWN') || input.isDown('s')) {
            this.y += dt * PLAYER_SPEED;
        }
        if (input.isDown('UP') || input.isDown('w')) {
            this.y -= dt * PLAYER_SPEED;
        }
        if (input.isDown('LEFT') || input.isDown('a')) {
            this.x -= dt * PLAYER_SPEED;
        }
        if (input.isDown('RIGHT') || input.isDown('d')) {
            this.x += dt * PLAYER_SPEED;
        }
    }

    checkBounds(height, width) {
        if (this.x < 0) {
            this.x = 0;
        } else if (this.x > width - this.sprite.componentWidth) {
            this.x = width - this.sprite.componentWidth;
        }
        if (this.y < 0) {
            this.y = 0;
        } else if (this.y > height - this.sprite.height) {
            this.y = height - this.sprite.height;
        }
    }
}










