const ENEMY_SPEED = 100;
const ENEMY_SIZE = 30;

export default class Enemy {
    constructor(x, y, img, width = ENEMY_SIZE, height = ENEMY_SIZE) {
        this.width = width;
        this.height = height;
        this.x = x;
        this.y = y;
        this.img = img[Math.round(Math.random() * -2 + 2)];
        this.angle = 0;
        this.direction = Math.round(Math.random() * -1 + 1);
    }

    updatePosition(dt) {//todo
        if (dt) {
            this.angle += Math.PI / 180;
            this.x -= dt * ENEMY_SPEED;
            if (this.direction) {
                this.y += dt * Math.sin(this.angle) * ENEMY_SPEED;
            } else {
                this.y += dt * -Math.sin(this.angle) *ENEMY_SPEED;
            }
        }
    }

    checkBounds() {
        return this.x < -this.width;
    }

    _collides(x, y, r, b, x2, y2, r2, b2) {
        return !(
            r <= x2
            || x > r2
            || b <= y2
            || y > b2
        );
    }

    checkCollisions(player) {
        return this._collides(player.x,
            player.y,
            player.x + player.sprite.componentWidth,
            player.y + player.sprite.height,
            this.x,
            this.y,
            this.x + this.width,
            this.y + this.height);
    }

    getSize() {
        return this.x;
    }

    render(context) {
        context.drawImage(this.img, this.x, this.y, this.width, this.height);
    }
}


