export default class Input {
    constructor() {
        this.pressedKeys = {};
        document.addEventListener('keydown', function(e) {
            this.setKey(e, true);
        }.bind(this));

        document.addEventListener('keyup', function(e) {
            this.setKey(e, false);
        }.bind(this));

        window.addEventListener('blur', function() {
            this.pressedKeys = {};
        }.bind(this));
    }

    isDown(key) {
        return this.pressedKeys[key.toUpperCase()];
    }

    setKey(event, status) {
        let code = event.keyCode;
        let key;

        switch(code) {
            case 32:
                key = 'SPACE'; break;
            case 37:
                key = 'LEFT'; break;
            case 38:
                key = 'UP'; break;
            case 39:
                key = 'RIGHT'; break;
            case 40:
                key = 'DOWN'; break;
            default:
                key = String.fromCharCode(code);
        }

        this.pressedKeys[key] = status;
    }
}


