import Input from '../Input';
import {
    changeGameStatus,
    decrementLives,
    resetLives
} from '../actions/gameStatus';
import {
    GAME_FIELD_WIDTH,
    PLAYER_IMAGE_WIDTH,
    PLAYER_IMAGE_HEIGHT,
    PLAYER_IMAGE_NUMBER_OF_FRAMES
} from '../constants';
import Player from './Player';
import Sprite from './Sprite';
import Enemy from './Enemy';

const score = document.querySelector('#score');
const lives = document.querySelector('#lives');

export default class Game {
    constructor(store, gameField, playerImages, enemyImages) {
        this.playerImages = playerImages;
        this.enemyImages = enemyImages;
        this.gameField = gameField;
        this.gameFieldContext = gameField.getContext('2d');
        this.store = store;
        this.player = {};
        this.lastTime = 0;
        this.input = new Input();
        this.enemies = [];
        this.totalGameTime = 0;
    }

    main() {
        if (this.store.getState().gameStatusReducer.gameStatus === 'START_GAME') {
            this.init(0);
            this.store.dispatch(changeGameStatus('GAME_STARTED'));
            this.store.dispatch(resetLives());
        }
        const now = Date.now();
        const dt = (now - this.lastTime) / 1000.0;
        if(this.store.getState().gameStatusReducer.lives === 0) {
            this.store.dispatch(changeGameStatus('GAME_FAILED'));
        }
        if (this.store.getState().gameStatusReducer.gameStatus === 'GAME_STARTED') {
            this.lastTime = now;
            this.updateGameField(dt);
            requestAnimationFrame(this.main.bind(this));
        }
    }

    init(dt) {
        const curLives =  this.store.getState().gameStatusReducer.lives - 1;
        const spriteNumber = curLives - 1  > 0 ?  curLives - 1 : 2;
        this.clearGameField();
        score.innerHTML = `Score: ${Math.round(this.totalGameTime)}`;
        lives.innerHTML = `Lives: ${this.store.getState().gameStatusReducer.lives}`;
        this.player = new Player(
            this.gameField.width / 2 ,
            this.gameField.height / 2,
            new Sprite({
                width: PLAYER_IMAGE_WIDTH,
                height: PLAYER_IMAGE_HEIGHT,
                image: this.playerImages[spriteNumber],
                numberOfFrames: PLAYER_IMAGE_NUMBER_OF_FRAMES,
                ticksPerFrame: PLAYER_IMAGE_NUMBER_OF_FRAMES * 2
            })
        );
        this.enemies = [];
        this.input = new Input();
        this.updateGameField(dt);
        this.totalGameTime = 0;
        this.lastTime = Date.now;
    }

    updateGameField(timeStamp) {
        const curLives = this.store.getState().gameStatusReducer.lives - 1;
        const spriteNumber = curLives >= 0 ? curLives : 2;
        this.clearGameField();
        this.totalGameTime += timeStamp ? timeStamp : 0;
        score.innerHTML = `Score: ${Number.parseInt(this.totalGameTime)}`;
        lives.innerHTML = `Lives: ${this.store.getState().gameStatusReducer.lives}`;
        this.player.sprite.image = this.playerImages[spriteNumber] ;
        this.player.update(this.gameFieldContext, this.input, timeStamp, this.gameField.height, this.gameField.width);
        this.addNewEnemy();
        this.updateEnemies(timeStamp);
    }

    clearGameField() {
        this.gameFieldContext.clearRect(0, 0, this.gameField.width, this.gameField.height)
    }

    addNewEnemy() {
        const randomYEnemyPosition = Math.random() * (this.gameField.height - 30) + 30;
        if (Math.random() < 1 - Math.pow(.9, this.totalGameTime / 100) && this.enemies.length < 50) {
            this.enemies.push(new Enemy(GAME_FIELD_WIDTH , randomYEnemyPosition, this.enemyImages));
        }
    }

    updateEnemies(dt) {
        for (let i = 0; i < this.enemies.length; i++) {
            this.enemies[i].updatePosition(dt);
            this.enemies[i].checkBounds(this.gameField.height);
            this.enemies[i].render(this.gameFieldContext);
            if (this.enemies[i].checkCollisions(this.player)) {
                this.store.dispatch(decrementLives());
                this.enemies = [];
                break;
            }
            if (this.enemies[i].checkBounds()) {
                this.enemies.shift();
            }
        }
    }
}


